.TopSection{
  background-image: url('/img/Path 11615.svg');
  height: 100vh;
  background-size: cover;
  background-repeat: no-repeat;
}

.NavigationList{
  display: flex;
}

.Navigation{
opacity: 1;
list-style-type: none;
margin: 1em;
}

.Logo{
margin-left: 83px;
width: 202px;
height: 74px;
background: transparent url('img/nav-logo.png') 0% 0% no-repeat padding-box;
opacity: 1;
}

.NavBar{
  display: flex;
  justify-content: space-between;
  align-items: center;
  height: 257px;
  width: 100vw;
  background: linear-gradient(#08365C, #06223900);
}

.TopSectionRight{
  margin-right: 2em;
  display: flex;
  justify-content: space-around;
}

a{
  text-decoration: none;
  color: #FFF;
}

h1{
  color: #FFF;
}

p{
  color: #FFF;
}

#GridTitle{
  color: #F1D9BF;
}

#GridPrice{
  font: italic normal bold 31px/41px 29LT;
  font-family: MyFont;
  border-radius: 5px;
  background-color: #FFF;
  color: #0A4473;
  padding: 7px;
  margin-right: 1em;
}

#GridIcons img{
  padding-right: 1em;
}

#GridIcons{
  display: flex;
  justify-content: flex-start;
  align-items: center;
}

#GridText{
  /* UI Properties */
text-align: left;
font: normal normal normal 17px/22px 29LT;
font-family: MyFont;
letter-spacing: 0px;
color: #FFFFFF;
opacity: 1;
}

